<style lang="scss">
    #goods {
        width: 100%;
        display: flex;
        height: 100%;
        #menu-wrapper {
            flex: 0 0 80px;
            background-color: #f3f5f7;
            position: relative;
            overflow-y: scroll;
            ul {
                width: 100%;
                li {
                    padding: 0 12px;
                    height: 50px;
                    font-size: 12px;
                    display: table;

                    div{
                        width: 56px;
                        padding: 12px 0;
                        display: table-cell;
                        vertical-align: middle;
                        border-bottom: 1px solid rgba(7,17,27,0.2);
                        span{
                            display: inline-block;
                            width: 12px;
                            height: 12px;
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                            margin-right: 2px;
                            &.decrease{
                                background-image: url("../assets/goodsImg/decrease.png");
                            }
                            &.discount{
                                background-image: url("../assets/goodsImg/discount.png");
                            }
                            &.special{
                                background-image: url("../assets/goodsImg/special.png");
                            }
                            &.invoice{
                                background-image: url("../assets/goodsImg/invoice.png");
                            }
                            &.guarantee{
                                background-image: url("../assets/goodsImg/guarantee.png");
                            }
                        }

                    }

                    &.active {
                        background-color: white;
                        font-weight: bold;
                        div{
                            border: none;
                        }
                    }

                }
            }


        }
        #foods-wrapper {
            flex: 1;
            //touch-action: none;
            position: relative;
            overflow-y: scroll;
            #title {
                background-color: #f3f5f7;
                border-left: 2px solid #d0dde1;
                padding-left: 10px;
                height: 30px;
                line-height: 30px;
                font-size: 14px;
                color: #93999f;
            }
            .food-list {
                .food-item {
                    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                    padding: 20px 0 18px 0;
                    margin: 0 20px;
                    display: flex;
                    &:last-child {
                        border: none;
                    }
                    .icon {
                        flex: 0 0 60px;
                        img {
                            width: 57px;
                            height: 57px;
                        }
                    }
                    .content {
                        flex: 1;
                        font-size: 10px;
                        margin-left: 10px;
                        color: #93999f;
                        position: relative;
                        h3 {
                            font-size: 14px;
                        }
                        div:nth-child(3) span {
                            margin-right: 10px;
                        }
                        .price {
                            span:first-child {
                                color: red;
                            }

                        }
                        .shoppingCart {
                            position: absolute;
                            right: 2px;
                            bottom: -6px;

                        }

                    }

                }
            }

        }
    }


</style>
<template>
    <div id="goods">
        <div id="menu-wrapper">
            <ul>
                <li v-for="(item,index) in goods" @click="scrollTo(index)" :class="{active:ActiveIndex===index}"
                    :key="index">
                    <div><span v-show="item.type!=-1" :class="icons[item.type]"></span>{{item.name}}</div>
                </li>
            </ul>
        </div>
        <div id="foods-wrapper" ref="foodsWrapper">
            <ul>
                <li v-for="(item,index) in goods" :key="index" class="food-list" :id="'item'+index">
                    <div id="title">{{item.name}}</div>
                    <ul>
                        <li class="food-item" @click="showFoodInfo(food)" v-for="(food,i) in item.foods" :key="i">
                            <div class="icon">
                                <img :src="food.icon" alt="">
                            </div>
                            <div class="content">
                                <h3>{{food.name}}</h3>
                                <div>{{food.description}}</div>
                                <div>
                                    <span>月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span>
                                        ￥{{food.price}}
                                    </span>
                                    <span v-if="food.oldPrice"><del>{{food.oldPrice}}</del></span>
                                </div>
                                <shoppingcart v-if="food" :food="food"></shoppingcart>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <foodinfo :food="selectFood" ref="foodInfo"></foodinfo>
    </div>

</template>
<script>
    //import IScroll  from 'iscroll'
    //import bscroll from "better-scroll"
    import foodinfo from "../components/food"
    import shoppingcart from "../components/shoppingCart"

    export default {
        name: 'goods',
        props: ["goods"],
        data() {
            return {
                foodListHeights: [],
                ActiveIndex: 0,
                selectFood: {},
                icons:[
                    "decrease",
                    "discount",
                    "special",
                    "invoice",
                    "guarantee",
                ]
            }
        },
        components: {
            "foodinfo": foodinfo,
            "shoppingcart": shoppingcart
        },
        mounted() {
            let _this = this;
            this.$nextTick(() => {
                _this.calcHeight();
            });

            this.$refs.foodsWrapper.onscroll = function () {
                let currentY = this.scrollTop;
                for (let i = 0; i < _this.foodListHeights.length - 1; i++) {
                    let heightBottom = _this.foodListHeights[i];
                    let heightTop = _this.foodListHeights[i + 1];
                    //对滑动后currentY值不足的情况进行修正
                    let diff = Math.abs(currentY - heightTop);
                    if (diff < 5) {
                        currentY = heightTop;
                    }
                    //判断currentY当前所在的区间
                    if (currentY < heightTop && currentY >= heightBottom) {
                        _this.ActiveIndex = i;
                    }
                }
            }
        },
        methods: {
            calcHeight() {
                let dom = this.$refs.foodsWrapper;
                let foodList = dom.getElementsByClassName("food-list");
                let height = 0;
                this.foodListHeights.push(height);
                for (let i = 0; i < foodList.length; i++) {
                    height += foodList[i].clientHeight;
                    this.foodListHeights.push(height);
                }
            },
            scrollTo(index) {
                let foodsWrapper = this.$refs.foodsWrapper;
                foodsWrapper.scrollTop = this.foodListHeights[index];
                this.ActiveIndex = index;
            },
            showFoodInfo(food) {
                this.selectFood = food;
                this.$refs.foodInfo.showFood();
            },

        },

    }
</script>
