/*
    发送请求
 */
import axios from "axios"
function http(url,methods,data) {
    return new Promise((resolve, reject) => {
        // 发送 POST 请求
        axios({
            method: methods,
            url: url,
            data: data,
            baseUrl:"http://127.0.0.1:3000/",
        }).then((res)=>{
            resolve(res)
        }).catch((err)=>{
            reject(err)
        });

    })
}

export default http;

