<style lang="scss">
    #ratings {
        height: 100%;
        position: relative;
        overflow-y: scroll;
        #comprehensiveScore {
            display: flex;
            padding: 18px 0;
            .left {
                flex: 0 0 140px;
                text-align: center;
                color: #93999f;
                font-size: 12px;
                div:first-child {
                    color: #f90;
                    font-size: 24px;
                    line-height: 28px;
                }
            }
            .right {
                flex: 1;
                > div {
                    font-size: 12px;
                    margin-bottom: 8px;
                    span {
                        margin: 0 5px;
                    }
                    .star {
                        display: inline-block;
                        span {
                            margin: 0 5px;
                            width: 14px;
                            height: 14px;
                        }
                    }
                    span:last-child {
                        color: #f90;
                    }
                }
                > div:last-child span:last-child {
                    color: #93999f;
                }
            }
        }

        #rating-wrapper {
            padding: 0 18px;
            li {
                display: flex;
                padding: 18px 0;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                position: relative;

                .avatar {
                    flex: 0 0 30px;
                    margin-right: 10px;
                    img {
                        width: 100%;
                        border-radius: 50%;
                    }
                }
                .content {
                    flex: 1;
                    font-size: 12px;
                    > div {
                        margin-bottom: 6px;
                    }
                    .delivery {

                        .star {
                            display: inline-block;
                            span {
                                width: 12px;
                                height: 12px;
                                margin: 0 5px;
                            }
                        }
                        span {
                            color: #93999f;
                        }
                    }
                    .recommend {
                        span {
                            display: inline-block;
                            border: 1px solid rgba(7, 17, 27, 0.1);
                            color: #93999f;
                            padding: 1px 6px;
                            border-radius: 2px;
                            margin: 0 8px 4px 0;
                        }
                        span:first-child {
                            color: #00a0dc;
                        }
                    }
                    .rateTime {
                        position: absolute;
                        top: 18px;
                        right: 8px;
                    }
                }
            }
        }

    }

</style>

<template>
    <div id="ratings">
        <div id="comprehensiveScore">
            <div class="left">
                <div>{{seller.score}}</div>
                <div>综合评分</div>
                <div>高于周边商家{{seller.rankRate}}</div>
            </div>
            <div class="right">
                <div>
                    <span>服务态度</span>
                    <star :score="seller.serviceScore"></star>
                    <span>{{seller.serviceScore}}</span>
                </div>
                <div>
                    <span>商品评分</span>
                    <star :score="seller.foodScore"></star>
                    <span>{{seller.foodScore}}</span>
                </div>
                <div>
                    <span>送达时间</span>
                    <span>{{seller.deliveryTime}}分钟</span>
                </div>
            </div>

        </div>
        <div class="split"></div>

        <ratingselect @typeindex="typeIndex" @hascontent="hasContent" :ratings="ratings"
                      :evaluates="evaluates"></ratingselect>
        <div id="rating-wrapper">
            <ul>
                <li v-for="(rating,index) in ratings" :key="index" v-show="needShow(rating.rateType,rating.text)">
                    <div class="avatar">
