        methods: {
            needShow(type, text) {
                if (this.type_index === 0) {
                    if (this.has_content) {
                        if (text !== "") {
                            return true;
                        } else {
                            return false;
                        }
                    }
                    return true;
                }
                if (this.type_index === 1) {
                    if (this.has_content) {
                        if (text !== "" && type === 0) {
                            return true;
                        } else {
                            return false;
                        }
                    }
                    if (type === 0) {
                        return true;
                    }
                }
                if (this.type_index === 2) {
                    if (this.has_content) {
                        if (text !== "" && type === 1) {
                            return true;
                        } else {
                            return false;
                        }
                    }
                    if (type === 1) {
                        return true;
                    }
                }
            },
            typeIndex(index) {
                this.type_index = index;
            },
            hasContent(has) {
                this.has_content = has;
            },
            format(time){
