<template>
    <div class="star">
        <span v-for="(star,index) in stars" :key="index" :class="star"></span>
    </div>
</template>

<script>
    export default {
        name: 'star',
        props:["score"],
        computed: {
            stars() {
                let detailStar = [];
                let score = Math.floor(this.score);
                for (let i = 0; i < score; i++) {
                    detailStar.push("on")
                }
                if (score % 1 > 0.5) {
                    detailStar.push("half")
                }
                while (detailStar.length < 5) {
                    detailStar.push("off")
                }
                return detailStar;
            }

        },
    }
</script>

<style lang="scss">
    .star {
        display: flex;
        justify-content: space-around;
        span {
            display: inline-block;
            width: 20px;
            height: 20px;
            margin: 0 10px;
            &.on {
                background: url("../assets/headerImg/star24_on.png") no-repeat;
                background-size: 100% 100%;
            }
            &.half {
                background: url("../assets/headerImg/star24_half.png") no-repeat;
                background-size: 100% 100%;
            }
            &.off {
                background: url("../assets/headerImg/star24_off.png") no-repeat;
                background-size: 100% 100%;
            }

        }
    }



</style>