<style lang="scss">
    #ratingSelect {
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        #ratingType {
            padding: 16px 0;
            margin: 0 16px;
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
            span {
                display: inline-block;
                padding: 8px 12px;
                background-color: #007bff;
                opacity: 0.4;
                color: white;
                font-size: 12px;
                margin-right: 10px;
                border-radius: 2px;

                &.active {
                    opacity: 1;
                }
            }
            span:last-child {
                background-color: rgba(77, 85, 93, 0.4);
            }
        }
        #showContent {
            padding: 16px;
            color: #93999f;
            span:first-child {
                margin-right: 5px;
                font-size: 20px;
                display: inline-block;
                vertical-align: top;

                &.active {
                    color: #00c850;
                }
            }
            span:last-child {
                font-size: 12px;
            }
        }
    }

</style>

<template>
    <div id="ratingSelect">
        <div id="ratingType">
            <span @click="switchType(index)" v-for="(item,index) in evaluates" :key="index"
                  :class="{active:selectType==index}">
                {{item}}&nbsp;&nbsp;{{satisfaction[index]}}
            </span>
        </div>
        <div id="showContent" @click="checkContent">
            <span :class="{active:showContent}">
                <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-check-circle-fill" fill="currentColor"
                     xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd"
                          d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
                </svg>
            </span>
            <span>只看有内容的评价</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'ratingSelect',
        props: ["ratings", "evaluates"],
        data() {
            return {
                selectType: 0,
                showContent: false
            }
        },
        methods: {
            switchType(index) {
                this.selectType = index;
                this.$emit("typeindex", index)
            },
            checkContent() {
                this.showContent = !this.showContent;
                this.$emit("hascontent", this.showContent)
            }
        },
        computed: {
            satisfaction() {
                let score = [];
                score.push(this.ratings.length, 0, 0);
                for (let i = 0; i < this.ratings.length; i++) {
                    if (this.ratings[i].rateType === 0) {
                        score[1]++;
                    } else {
                        score[2]++;
                    }
                }
                return score;
            }
        }
    }
</script>
