<style lang="scss">
    #vueHeader {
        width: 100%;
        position: relative;
        #background {
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: -1;
            img {
                width: 100%;
                height: 100%;
                filter: blur(10px);
            }
        }
        #v-header {
            padding: 20px;
            background-color: rgba(0, 0, 0, 0.5);
            display: flex;
            #avatar img {
                flex: 0;
                width: 64px;
            }
            #content {
                flex: 1;
                margin-left: 20px;
                color: white;
                font-size: 12px;
                div {
                    margin: 5px 0;
                }
                div:first-child {

                    line-height: 21px;
                    vertical-align: middle;
                    h1 {
                        span:first-child {
                            width: 30px;
                            height: 18px;
                            display: inline-block;
                            margin-right: 8px;
                            img {
                                width: 100%;
                                height: 100%;
                            }
                        }
                        span:last-child {
                            font-size: 16px;
                            display: inline-block;
                            margin-top: -1px;
                            vertical-align: top;
                        }
                    }

                }
                div:last-child {
                    span {
                        font-size: 12px;
                        margin-right: 5px;
                        img {
                            width: 12px;
                            height: 12px;
                        }
                    }
                }
            }
        }
        #notice {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            background-color: rgba(0, 0, 0, 0.6);
            color: white;
            height: 30px;
            font-size: 12px;
            line-height: 30px;
            padding: 0 30px 0 10px;
            span:first-child {
                display: inline-block;
                margin-right: 5px;
                img {
                    width: 22px;
                    height: 12px;
                }
            }
            span:nth-child(2) {
                margin-right: 50px;
            }
            span:last-child {
                position: absolute;
                bottom: 0;
                right: 10px;
            }
        }
        #support-count {
            position: absolute;
            bottom: 40px;
            right: 20px;
            background-color: rgba(0, 0, 0, 0.6);
            border-radius: 20px;
            color: white;
            padding: 3px 8px;
            font-size: 12px;
        }
        #detail {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 10;
            background-color: rgba(0, 0, 0, 0.8);
            color: white;
            font-size: 12px;
            line-height: 20px;
            //transition: all 1s;

            &.fade-enter-active {
                animation: bounce-in .5s;
            }
            &.fade-leave-active {
                animation: bounce-in .5s reverse;
            }
            @keyframes bounce-in {
                from {
                    opacity: 0;
                }
                to {
                    opacity: 1;
                }
            }

            h2 {
                text-align: center;
                font-size: 24px;
                margin: 60px auto 10px auto;
            }

            div {
                width: 80%;
                margin: 20px auto;
            }

            .title {
                display: flex;
                justify-content: space-around;
                width: 80%;
                .line {
                    flex: 1;
                    display: inline-block;
                    position: relative;
                    top: -8px;
                    border-bottom: 1px solid white;
                }

                .text {
                    padding: 0 10px;
                }
            }

            ul li {
                margin: 10px 0;
                span:first-child {
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    margin-right: 8px;
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    &.decrease {
                        background-image: url("../assets/headerImg/decrease.png");
                    }
                    &.discount {
                        background-image: url("../assets/headerImg/discount.png");
                    }
                    &.special {
                        background-image: url("../assets/headerImg/special.png");
                    }
                    &.invoice {
                        background-image: url("../assets/headerImg/invoice.png");
                    }
                    &.guarantee {
                        background-image: url("../assets/headerImg/guarantee.png");
                    }
                }
            }

            div:last-child {
                margin-top: 30px;
                text-align: center;
            }
            #closeDetail {
                display: inline-block;
                font-size: 48px;
            }

        }
    }
</style>
<template>
    <div id="vueHeader">
        <div id="background">
            <img :src="seller.avatar" alt="">
        </div>
        <div id="v-header">
            <div id="avatar">
                <img :src="seller.avatar" alt="" align="">
            </div>
            <div id="content">
                <div>
                    <h1>
                        <span>
                            <img src="../assets/headerImg/brand.png" alt="">
                        </span>
                        <span>
                            {{seller.name}}
                        </span>
                    </h1>
                </div>
                <div>{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
                <div>
                    <span><img src="../assets/headerImg/decrease.png" alt=""></span>
                    <span v-if="seller.supports">{{seller.supports[0].description}}</span>
                </div>
            </div>
        </div>
        <div id="notice" @click="showDetail">
            <span><img src="../assets/headerImg/bulletin.png" alt=""></span>
            <span>{{seller.bulletin}}</span>
            <span>＞</span>
        </div>
        <div id="support-count" @click="showDetail" v-if="seller.supports">{{seller.supports.length}}个 ＞</div>
        <transition name="fade">
            <div v-show="detailShow" id="detail">
                <h2>{{seller.name}}</h2>

                <star :score="seller.score"></star>

                <div class="title">
                    <span class="line"></span>
                    <span class="text">优惠信息</span>
                    <span class="line"></span>
                </div>
                <div>
                    <ul>
                        <li v-for="item in seller.supports" :key="item.type">
                            <span :class="detailImgs[item.type]"></span>
                            <span>{{item.description}}</span>
                        </li>
                    </ul>
                </div>
                <div class="title">
                    <span class="line"></span>
                    <span class="text">商家公告</span>
                    <span class="line"></span>
                </div>
                <div>
                    {{seller.bulletin}}
                </div>

                <div>
                <span @click="closeDetail" id="closeDetail">
                    <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-x" fill="currentColor"
                         xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd"
                              d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </span>

                </div>

            </div>
        </transition>
    </div>


</template>

<script>
    import star from "./star"

    export default {
        name: 'vueHeader',
        props: ["seller"],
        data() {
            return {
                detailShow: false,
                detailImgs: [
                    "decrease",
                    "discount",
                    "special",
                    "invoice",
                    "guarantee",
                ]
            }
        },
        methods: {
            showDetail() {
                this.detailShow = true;
            },
            closeDetail() {
                this.detailShow = false;
            },
        },
        components: {
            "star": star,
        },
    }
</script>

<style scoped>

</style>