<style lang="scss">
    #sells {
        height: 100%;
        position: relative;
        overflow-y: scroll;
        width: 100%;
        h2 {
            font-size: 18px;
            font-weight: 500;
            padding-bottom: 8px;
        }
        h4 {
            font-size: 16px;
            font-weight: 500;
        }
        #sellerInfo {
            padding: 20px;

            #desc {
                padding-bottom: 18px;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                color: #4d555d;
                .star {
                    display: inline-block;
                    span {
                        margin: 0 5px;
                        width: 12px;
                        height: 12px;
                    }
                }
                span {
                    margin-left: 10px;
                    display: inline-block;
                    font-size: 12px;
                }
            }
            #remark {
                display: flex;
                padding-top: 18px;
                text-align: center;
                > div {
                    flex: 1;
                    border-right: 1px solid rgba(7, 17, 27, 0.1);
                    h4 {
                        color: #93999f;
                        font-size: 14px;
                    }
                    div span {
                        font-size: 24px;
                    }
                    &:last-child {
                        border: none;
                    }
                }
            }
            #love {
                position: absolute;
                width: 50px;
                right: 30px;
                top: 20px;
                text-align: center;
                div:first-child {
                    color: #d4d6d9;
                    font-size: 22px;
                    line-height: 22px;
                    &.collected {
                        color: red;
                    }
                }
                div:last-child {
                    color: #4d555d;
                    font-size: 12px;
                }

            }
        }
        #sellerNotice {
            padding: 18px;

            .content {
                font-size: 12px;
                line-height: 24px;
                color: red;
                padding: 10px 20px;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
            }
            .supports li {
                padding: 16px 12px;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                font-size: 12px;
                line-height: 16px;
            }
        }

        #sellerRealScene {
            padding: 20px;
            //width: 100%;

            #pics {
                overflow-x: scroll;
                //white-space: nowrap;
                ul {
                    display: flex;
                    flex-wrap: nowrap;
                    li {
                        //display: inline-block;
                        margin-right: 6px;
                        width: 120px;
                        height: 90px;
                        flex-shrink: 0;
                        img {
                            //width: 120px;
                            //height: 90px;
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }

        }

        #sellerInfos {
            padding: 20px;

            h2 {
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                padding-bottom: 16px;
            }
            ul li {
                padding: 16px 12px;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                font-size: 12px;
                line-height: 16px;
            }
        }
    }

</style>
<template>
    <div id="sells">
        <div id="sellerInfo">
            <h2>{{seller.name}}</h2>
            <div id="desc">
                <star :score="seller.score"></star>
                <span>({{seller.ratingCount}})</span>
                <span>月售{{seller.sellCount}}单</span>
            </div>
            <div id="remark">
                <div>
                    <h4>起送价</h4>
                    <div>
                        <span>{{seller.minPrice}}</span>元
                    </div>
                </div>
                <div>
                    <h4>商家配送</h4>
                    <div>
                        <span>{{seller.deliveryPrice}}</span>元
                    </div>
                </div>
                <div>
                    <h4>平均配送时间</h4>
                    <div>
                        <span>{{seller.deliveryTime}}</span>分钟
                    </div>
                </div>
            </div>
            <div id="love" @click="collection">
                <div :class="{collected:isCollection}">
                    <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-heart-fill" fill="currentColor"
                         xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd"
                              d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>
                    </svg>
                </div>
                <div>{{!isCollection ? "收藏" : "已收藏"}}</div>
            </div>
        </div>
        <div class="split"></div>
        <div id="sellerNotice">
            <h2>商家公告</h2>
            <div class="content">
                {{seller.bulletin}}
            </div>
            <ul class="supports">
                <li v-for="item in seller.supports" :key="item.type">
                    <span></span><span>

                </span>
                    {{item.description}}
                </li>
            </ul>
        </div>
        <div class="split"></div>
        <div id="sellerRealScene">
            <h2>商家实景</h2>
            <div id="pics">
                <ul>
                    <li v-for="(item,index) in seller.pics" :key="index">
                        <img :src="item" alt="">
                    </li>
                </ul>
            </div>

        </div>
        <div class="split"></div>
        <div id="sellerInfos">
            <h2>商家信息</h2>
            <ul>
                <li v-for="(item,index) in seller.infos" :key="index">{{item}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
    import star from "../components/star"

    export default {
        name: 'sells',
        props: ["seller"],
        components: {
            "star": star
        },
        data() {
            return {
                isCollection: false,
                icons:[
                    "decrease",
                    "discount",
                    "special",
                    "invoice",
                    "guarantee",
                ]
            }
        },
        methods: {
            collection() {
                this.isCollection = !this.isCollection;
            }
        }
    }
</script>