<style lang="scss">

    .reduce {
        display: inline-block;
        padding: 0 10px;
        line-height: 16px;
        font-size: 20px;
        color: #00a0dc;
        &.move-enter-active {
            animation: reduce-move .7s;
        }
        &.move-leave-active {
            animation: reduce-move .7s reverse;
        }
        @keyframes reduce-move {
            from {
                opacity: 0;
                transform: translate3d(60px, 0, 0) rotate(180deg) ;
            }
        }
    }

    .count {
        display: inline-block;
        width: 14px;
        font-size: 14px;
        vertical-align: top;
        text-align: center;
        line-height: 22px;
        color: #93999f;
        span{
            margin: 0;
            text-align: center;
        }
    }

    .add {
        display: inline-block;
        padding: 0 10px;
        line-height: 18px;
        font-size: 20px;
        color: #00a0dc;
    }

</style>
<template>

    <div class="shoppingCart">
        <transition name="move">
            <div class="reduce" @click.stop.prevent="reduce" v-show="foodCopy.count>0">
                <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-dash-circle"
                     fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd"
                          d="M8 15A7 7 0 1 0 8 1a7 7 0 0 0 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                    <path fill-rule="evenodd"
                          d="M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z"/>
                </svg>
            </div>
        </transition>
        <div class="count"><span v-show="foodCopy.count>0">{{foodCopy.count}}</span></div>
        <div class="add" @click.stop.prevent="add">
            <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-plus-circle-fill"
                 fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                <path fill-rule="evenodd"
                      d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
            </svg>
        </div>
    </div>

</template>

<script>
    export default {
        name: 'shoppingCart',
        props: ["food"],
        methods: {
            add() {
                this.foodCopy.count++;
                //this.$root.$emit('add_cart');
            },
            reduce() {
                this.foodCopy.count--;
            }
        },
        computed: {
            foodCopy() {
                return this.food;
            }
        }
    }
</script>

<style scoped>

</style>