<style lang="scss">
    #vueFooter {
        #cartContent {
            position: fixed;
            height: 50px;
            width: 100%;
            bottom: 0;
            background-color: #141d27;
            display: flex;
            z-index: 5;
            #calculatePrice {
                flex: 1;
                display: flex;
                #logoWrapper {
                    width: 56px;
                    height: 56px;
                    border-radius: 50%;
                    position: relative;
                    top: -10px;
                    margin: 0 12px;
                    background-color: #141d27;
                    #logo {
                        width: 44px;
                        height: 44px;
                        position: absolute;
                        top: 6px;
                        left: 6px;
                        background-color: #2b343c;
                        border-radius: 50%;
                        color: #80858a;
                        line-height: 44px;
                        text-align: center;
                        font-size: 24px;
                        &.active {
                            background-color: #00a0dc;
                            color: white;
                        }
                    }
                    #shopCount {
                        width: 20px;
                        height: 15px;
                        line-height: 15px;
                        font-size: 12px;
                        background-color: red;
                        color: white;
                        border-radius: 10px;
                        position: absolute;
                        top: 0;
                        right: 0;
                        text-align: center;
                    }
                }
                #price {
                    font-size: 16px;
                    color: rgba(255, 255, 255, 0.4);
                    font-weight: bold;
                    margin: 12px 0 12px 12px;
                    line-height: 24px;
                    padding-right: 18px;
                    border-right: 1px solid rgba(255, 255, 255, 0.1);
                }
                #description {
                    font-size: 12px;
                    color: rgba(255, 255, 255, 0.4);
                    font-weight: bold;
                    margin: 12px;
                    line-height: 24px;
                }
            }
            #settlement {
                flex: 0 0 105px;
                background-color: #2b333b;
                text-align: center;
                line-height: 50px;
                font-size: 12px;
                font-weight: bold;
                color: rgba(255, 255, 255, 0.4);

                &.active {
                    background-color: #00b43c;
                    color: white;
                }
            }
        }
        .ball-container {
            .ball {
                position: fixed;
                bottom: 22px;
                left: 32px;
                z-index: 150;
                transition: all 0.4s;
                transform: cubic-bezier(.34, -0.56, .73, .62);
                .inner {
                    width: 16px;
                    height: 16px;
                    border-radius: 50%;
                    background: rgb(0, 160, 220);
                    transition: all 0.4s linear;
                }

            }

        }

        #shopCartList {
            position: absolute;
            bottom: 50px;
            left: 0;
            width: 100%;
            z-index: 3;
            //display: none;
            &.fold-enter-active {
                animation: fold-in .5s;
            }
            &.fold-leave-active {
                animation: fold-in .5s reverse;
            }
            @keyframes fold-in {
                from {
                    transform: translate3d(0, 100%, 0)
                }
                to {
                    transform: translate3d(0, 0, 0)
                }
            }
            #shopCartTitle {
                display: flex;
                background-color: #f3f5f7;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                height: 40px;
                line-height: 40px;
                padding: 0 18px;
                .title {
                    flex: 1;
                    font-size: 14px;
                }
                .dels {
                    flex: 0 0 30px;
                    font-size: 12px;
                    span {
                        color: #00a0dc;
                        text-decoration: none;
                    }
                }

            }
            #shopCartContent {
                padding: 0 18px;
                background-color: white;
                max-height: 250px;
                overflow-y: scroll;
                ul {

                    li {
                        display: flex;
                        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                        padding: 12px 0;
                        position: relative;
                        .name {
                            flex: 1;
                            font-size: 12px;
                            line-height: 24px;
                            color: #07111b;
                        }
                        .price {
                            flex: 0 0 15px;
                            color: red;
                        }
                        .shoppingCart {
                            flex: 0 0 95px;
                        }
                    }
                }
            }
        }
        #mask {
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: 2;
            background-color: rgba(7, 17, 27, 0.7);

            &.fade-enter-active {
                animation: bounce-in .5s;
            }
            &.fade-leave-active {
                animation: bounce-in .5s reverse;
            }
            @keyframes bounce-in {
                from {
                    opacity: 0;
                }
                to {
                    opacity: 1;
                }
            }

        }
    }
</style>
<template>
    <div id="vueFooter">
        <div id="cartContent">
            <div id="calculatePrice">
                <div id="logoWrapper">
                    <div id="logo" @click="showCart" :class="{active:cart.length>0}">
                        <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-cart3" fill="currentColor"
                             xmlns="http://www.w3.org/2000/svg">
                            <path fill-rule="evenodd"
                                  d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .49.598l-1 5a.5.5 0 0 1-.465.401l-9.397.472L4.415 11H13a.5.5 0 0 1 0 1H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l.84 4.479 9.144-.459L13.89 4H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm7 0a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>
                        </svg>
                    </div>
                    <div id="shopCount" v-if="cart.length>0">{{cart.length}}</div>
                </div>
                <div id="price">￥{{totalPrice}}</div>
                <div id="description">另需配送费{{seller.deliveryPrice}}元</div>
            </div>
            <div id="settlement" :class="{active:totalPrice>seller.minPrice}">
                {{settlement()}}
            </div>
        </div>
        <!--<div class="ball-container">
            <div v-for="(ball,index) in balls" :key="index">
                <transition name="drop" v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:after-enter="afterEnter">
                    <div class="ball" v-show="ball.show">
                        <div class="inner inner-hook"></div>
                    </div>
                </transition>
            </div>
        </div>-->
        <transition name="fold">
            <div id="shopCartList" v-show="cartState&&cart.length>0">
                <div id="shopCartTitle">
                    <div class="title">购物车</div>
                    <div class="dels"><span @click="clearCart">清空</span></div>
                </div>
                <div id="shopCartContent">
                    <ul>
                        <li v-for="(item,index) in cart" :key="index">
                            <div class="name">{{item.name}}</div>
                            <div class="price">￥{{item.price}}</div>
                            <shoppingCart :food="item"></shoppingCart>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
        <transition name="fade">
            <div id="mask" v-show="cartState&&cart.length>0" @click="showCart"></div>
        </transition>
    </div>
</template>

<script>
