        },
        created() {
            console.log(this.$root);
            //this.$root.$on('add_cart', this.drop);
        },
        watch: {
            cart() {
                if (this.cart.length === 0) {
                    this.cartState = false;
                }
            }
        },
        computed: {
            cart() {
                let shoppingCart = [];
                this.goods.forEach(function (item) {
                    item.foods.forEach(function (t) {
                        if (t.count && t.count !== 0) {
                            shoppingCart.push(t)
                        }
                    })
                });
                return shoppingCart;
            },
            totalPrice() {
                let total = 0;
                this.cart.forEach(function (t) {
                    total += t.price * t.count;
                });
                return total;
            },

        },
    }
</script>

